import React,{useEffect} from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { fetchUsers,deleteUsers,removeUsers } from '../../State/UserSlice'

function Users() {

    const dispatch = useDispatch();

    const {data:users} = useSelector((state) => state.users)

 

    useEffect(()=>{
        dispatch(fetchUsers())
  // eslint-disable-next-line react-hooks/exhaustive-deps
      },[])



    const handleRemove = (elementId) => {
      dispatch(removeUsers(elementId));
  };
  return (
    <div className="container py-5  users-container">
        <div className="users-section shadow py-4">
           <h1 className="text-center py-3">Users</h1>
          {/* Enter User id to delete: <input type="text" name="id" id="id" onMouseLeave={edValueKeyPress} /> */}
             <div className="user-content px-4">
<table className="table">
  <thead>
    <tr>
      <th scope="col">#</th>
      <th scope="col">User Id</th>
      <th scope="col">First Name</th>
      <th scope="col">Last Name</th>
      <th scope="col">Email</th>
      <th scope="col">Delete User</th>
    </tr>
  </thead>

 <tbody>
      {users.map((users,id)=>{ 
       //{console.log(users._id)} 
         return   <tr key={users._id}>
           <th scope="row">{id}</th>
           <td>{users._id}</td>
           <td >{users.fname}</td>
           <td>{users.lname}</td>
           <td>{users.email}</td>
           <td><button className="btn btn-danger" onClick={()=>{ dispatch(deleteUsers(users._id));handleRemove(users._id)}} >Delete</button></td>
         </tr>
 })}
</tbody>
  

</table>
             </div>
        </div>

    </div>
  )
}

export default Users