import React, { useEffect,useState} from 'react'
import '../App.css'
import { useSelector, useDispatch } from 'react-redux'
import { STATUSES } from '../State/moviesSlice';
import { add } from '../State/cartSlice';
import { fetchMovies,fetchmoviespagination  } from '../State/moviesSlice'
import Filter from './Filter'
import { useAlert } from 'react-alert'
import "../App.css"
import ReactPaginate from "react-paginate"
// import { useState } from 'react';



function Movies() {

    const dispatch = useDispatch();
     const alert = useAlert()

    const { paginateData: movies,data:allmovies, status } = useSelector((state) => state.movies)
    console.log("staus",status)

console.log("moviesdata",movies)

   //getting total pages from api for pagination
    const totalmoviesPages=Math.ceil(allmovies.length/8);
    console.log("totalpagss",totalmoviesPages)


    useEffect(() => {
        dispatch(fetchMovies());
           dispatch(fetchmoviespagination());
           // eslint-disable-next-line
    }, [])

   
    const handleAdd = (movie) => {
        dispatch(add(movie));
        localStorage.getItem('token') ? alert.success("movie added to favourite"):alert.error("You have to login to perfrom this action")
  
    };

const handlePageClick=async(pageNumber)=>{
    // console.log("page click",pageNumber)
    // console.log("page number",pageNumber.selected)
    let limit=8;
    let cuurentPage=pageNumber.selected+1;
    console.log("currentpage",cuurentPage)
    dispatch(fetchmoviespagination(cuurentPage))

}

    return (
        <>

            <div className="container-fluid movies-container">
                <h1>Explore Our Latest Movies</h1>
                <div className="movies-content d-flex pb-5">
                    {/*========================Filter section ==========================*/}
                   
                    <Filter/>
                    {/*======================== movies section ========================*/}

        {status===STATUSES.ERROR? <h4 className="mt-3 text-danger d-inline-block error-msg w-75">Something wrong happend!</h4>:""}
  
 
 { !(status === STATUSES.LOADING) ?   <div className="row movies-items row-cols-1 row-cols-2 row-cols-sm-3 row-cols-md-3 row-cols-lg-4   g-4 w-75">
 {movies.length===0 && status==="idle" ? <h1 className="text-danger fs-3 w-100  d-flex align-items-center justify-content-center ">nothing to show!</h1> : movies.map((element, id) => {
                  return  <div className="col" key={element._id} >
                               
                           <div className="card border-0 rounded-0 shadow-sm" >
                                    <img src={element.img} className="card-img-top" alt="..." />
                                    <div className="card-body py-4">
                                        <div className='d-flex justify-content-between'>
                                        <h6 className="py-1 "><span className="fa fa-star text-warning pe-2"></span>{element.rating}</h6>
                                        <button className="px-0 py-0 bg-transparent  border-0 text-danger  fs-2 " onClick={() => handleAdd(element)}>
                                      <i className={`bx bx-bookmark `}></i> 
                                       
                                        </button>
                                        </div>
                                        <h6 className="card-title pb-4 fs-6 fw-light">{element.name}</h6>
                                        <button className="btn btn-outline-danger movies-btn"><i className="fa-solid fa-play pe-2"></i>Watch Trailer</button>
                                       
                                    </div>
                                </div>
                            </div> 


                        })} 
                    </div>:  <div className="d-flex py-5 w-100 justify-content-center align-items-center">
                             <div className="spinner-border text-danger" role="status">
                            <span className="visually-hidden">Loading...</span>
                             </div>
                         </div>}

                </div>

                <ReactPaginate
                previousLabel={"previous"}
                nextLabel={"next"}
                breakLabel={"..."}
                pageCount={totalmoviesPages}
                marginPagesDisplayed={2}
                pageRangeDisplayed={1}
                onPageChange={handlePageClick}
                containerClassName={"pagination justify-content-center "}
                pageClassName={"page-item"}
                pageLinkClassName={"page-link bg-dark z-index-1  text-light"}
                previousClassName={"page-item"}
                previousLinkClassName={"page-link bg-danger text-light"}
                nextClassName={"page-item"}
                nextLinkClassName={"page-link  bg-danger text-light"}
                breakClassName={"page-item"}
                breakLinkClassName={"page-link bg-dark text-light"}
                activeClassName={"active border-danger"}
               
      
                 


                />
            </div>

            

        </>
    )
}

export default Movies