import React, { useEffect } from 'react'
import { useNavigate } from 'react-router'
import { useFormik } from "formik"
import { loginSchema } from '../schemas'
import { useAlert } from 'react-alert'

function Login() {
  const alert = useAlert()
  const initialValues = {
    email: "",
    Password: ""
  }

  const { values, touched, errors, handleBlur, handleChange, handleSubmit, isSubmitting, resetForm } = useFormik({
    initialValues: initialValues,
    validationSchema: loginSchema,
    onSubmit: async (values) => {
      const response = await fetch(`/api/users/login`, {
        method: 'POST', // *GET, POST, PUT, DELETE, etc.
        headers: {
          'Content-Type': 'application/json',


        },
        body: JSON.stringify(values)
      });
      const json = await response.json()
      console.log(json)
      if (json.success) {
        localStorage.setItem('token', json.authtoken)
        alert.success("logged in success")
        navigate("/")
      } else {
        alert.error('logged in fail')
      }
      console.log(errors)
      resetForm();
    }
  })

  const navigate = useNavigate()

 
  return (
    <>
    <div className="login-section container py-5 ">
      <div className="login-container shadow  px-4 py-3 mx-auto w-50">
        <h1 className="text-center py-3"> Login</h1>
          <form onSubmit={handleSubmit}>

          <div className="form-outline mb-4">
            <input type="email" id="form2Example1"  value={values.email} onChange={handleChange} onBlur={handleBlur}  name="email" className="form-control" />
            <label className="form-label"  htmlFor="form2Example1">Email address</label>
          </div>


          <div className="form-outline mb-4">
            <input type="password" id="form2Example2" name="Password" value={values.Password} onChange={handleChange}  onBlur={handleBlur} className="form-control" />
            <label className="form-label"htmlFor="form2Example2">Password</label>
          </div>


          <div className="row mb-4">
            {/* <div className="col d-flex justify-content-center">
   
      <div className="form-check">
        <input className="form-check-input" type="checkbox" value="" id="form2Example34" checked />
        <label className="form-check-label" htmlFor="form2Example34"> Remember me </label>
      </div>
    </div> */}

            <div className="col">

              <a href="/">Forgot password?</a>
            </div>
          </div>

          <button type="submit" disabled={isSubmitting} className="btn btn-danger btn-block mb-4">Login</button>


          <div className="text-center">
            <p>Not a member? <a href="#!">Register</a></p>
            <p>or sign up with:</p>
            <button type="button" className="btn btn-danger btn-floating mx-1">
              <i className="fab fa-facebook-f"></i>
            </button>

            <button type="button" className="btn btn-danger btn-floating mx-1">
              <i className="fab fa-google"></i>
            </button>

            <button type="button" className="btn btn-danger btn-floating mx-1">
              <i className="fab fa-twitter"></i>
            </button>

            <button type="button" className="btn btn-danger btn-floating mx-1">
              <i className="fab fa-github"></i>
            </button>
          </div>
        </form>
      </div>
    </div>
    </>
  )
}

export default Login