const { createSlice, createAsyncThunk } = require('@reduxjs/toolkit');


export const STATUSES = Object.freeze({
    IDLE: 'idle',
    ERROR: 'error',
    LOADING: 'loading',
});

const moviesSlice= createSlice({
    name: 'movies',
    initialState: {
        data: [],
        searchData:[],
        paginateData:[],
        status: STATUSES.IDLE,
        search:"",
    },
    reducers: {
        // use with coustom thunk
        // setMovies(state, action) {
        //     state.data = action.payload;
        // },
        removeMovies(state,action){
          return{
          ...state,
            data: [...state.data].filter((movie)=>movie._id !== action.payload)

           }
        },
        getSearch(state,action){
              state.search=action.payload;
        },
        searchMovies(state,action){
          return{
            ...state,
              paginateData:[...state.searchData].filter((curElem)=>{

               return   curElem.name.toLowerCase().includes(action.payload)
              }) 
          }   
        
        },
        // setStatus(state, action) {
        //     state.status = action.payload;
        // },
       setPaginateMovies(state,action){
        state.paginateData = action.payload;
        console.log(state.paginateData)
       }
    },
    //use for dispatching in redux toolkit thunk
    //it give three actions just like promise i.e pending fulfilled,rejected
    extraReducers: (builder) => {
        builder
            .addCase(fetchMovies.pending, (state, action) => {
                state.status = STATUSES.LOADING;
            })
           builder.addCase(fetchMovies.fulfilled, (state, action) => {
                state.data = action.payload;
                state.paginateData=action.payload
                state.searchData=action.payload;
                state.status = STATUSES.IDLE;
            })
          builder.addCase(fetchMovies.rejected, (state, action) => {
                state.status = STATUSES.ERROR;
            })        
          
    },

});

