export function  fetchmoviespagination (currentPage){
  return async function  fetchUserThunk(dispatch,getState){
      try {
          const response= await fetch(`/api/movies/getmoviespagination?page=${currentPage}&limit=8`,{
              method: 'GET', // *GET, POST, PUT, DELETE, etc.
              headers: {
                'Content-Type': 'application/json',
              //   'auth-token': localStorage.getItem('token')
              
              },
             });
             const data= await response.json()
          dispatch(setPaginateMovies(data));
      } catch (err) {
          console.log(err);
      }
  };
}


