import React, { useState } from 'react'
import { useNavigate } from 'react-router'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Image from 'react-bootstrap/Image';
import img2 from '../images/img2.jpg'
import img3 from '../images/img3.jpg'
import { useFormik } from "formik"
import { signUpSchema } from '../schemas'
import "../App.css"
import { useAlert } from 'react-alert'

function Signup() {
  const alert = useAlert()
  const initialValues = {
    fname:"",
    lname:"",
    email:"",
    Password:"",
    CPassword:""
    
  }

  const { values, touched, errors, handleBlur, handleChange, handleSubmit, isSubmitting, resetForm } = useFormik({
    initialValues: initialValues,
    validationSchema: signUpSchema ,
    onSubmit: async (values) => {
      const response = await fetch(`/api/users/signup`, {
        method: 'POST', // *GET, POST, PUT, DELETE, etc.
        headers: {
          'Content-Type': 'application/json',


        },
        body: JSON.stringify(values)
      });
      const json = await response.json()
      console.log(json)
      if (json.success) {
        localStorage.setItem('token', json.authtoken)
        alert.success("logged in success")
        navigate("/")
      } else {
        alert.error('logged in fail')
      }
      console.log(errors)
      resetForm();
    }
  })

  const navigate = useNavigate()


 return(
  <div className="signup-section container py-5 ">
    <div className="signup-container shadow  px-4 py-3 mx-auto w-50">
      <h1 className="text-center py-3">SignUp</h1>

  <form onSubmit={handleSubmit}>
  <div className="row mb-4">
    <div className="col">
      <div className="form-outline">
        <input type="text" id="form3Example1" name="fname" value={values.fname} onChange={handleChange} onBlur={handleBlur}  className="form-control" />
        <label className="form-label"  htmlFor="form3Example1">First name</label>
      </div>
    </div>
    <div className="col">
      <div className="form-outline">
        <input type="text" id="form3Example2"  name="lname" value={values.lname} onChange={handleChange} onBlur={handleBlur}  className="form-control" />
        <label className="form-label" htmlFor="form3Example2">Last name</label>
      </div>
    </div>
  </div>

  <div className="form-outline mb-4">
    <input type="email" id="form3Example3" name="email"  value={values.email} onChange={handleChange} onBlur={handleBlur} className="form-control" />
    <label className="form-label"  htmlFor="form3Example3">Email address</label>
  </div>


  <div className="form-outline mb-4">
    <input type="password" id="form3Example4" name="Password"  value={values.Password} onChange={handleChange} onBlur={handleBlur}  className="form-control" />
    <label className="form-label"  htmlFor="form3Example4">Password</label>
  </div>
  <div className="form-outline mb-4">
    <input type="password" id="form3Example5" name="CPassword" value={values.CPassword} onChange={handleChange} onBlur={handleBlur}  className="form-control" />
    <label className="form-label"  htmlFor="form3Example4">Confirm Password</label>
  </div>


  {/* <div className="form-check d-flex justify-content-center mb-4">
    <input className="form-check-input me-2" type="checkbox" value="" id="form2Example33" checked />
    <label className="form-check-label" htmlFor="form2Example33">
      Subscribe to our newsletter
    </label>
  </div> */}


  <button type="submit" className="btn btn-danger btn-block w-100 mb-4">Sign up</button>


  <div className="text-center">
    <p>or sign up with:</p>
    <button type="button" className="btn btn-danger btn-floating mx-1">
      <i className="fab fa-facebook-f"></i>
    </button>

    <button type="button" className="btn btn-danger btn-floating mx-1">
      <i className="fab fa-google"></i>
    </button>

    <button type="button" className="btn btn-danger btn-floating mx-1">
      <i className="fab fa-twitter"></i>
    </button>

    <button type="button" className="btn btn-danger btn-floating mx-1">
      <i className="fab fa-github"></i>
    </button>
  </div>
</form>
     </div>
   </div>
  )
}

export default Signup