import { getNodeText } from '@testing-library/react';
import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { remove } from '../State/cartSlice';
import '../App.css'
const Cart = () => {
    const dispatch = useDispatch();
    const movies = useSelector((state) => state.cart);
    console.log(movies)

    const handleRemove = (elementId) => {
        dispatch(remove(elementId));
    };

    return (
        <div className="container py-5 favourite-cart">
            <h1 className="text-center py-5">Favourite Movies List</h1>

            <div className="row row-cols-1 row-cols-2 row-cols-sm-3 row-cols-md-3 row-cols-lg-4  g-4 w-100">
                {movies.map((element) => (
                    <div key={(element._id)} className="col">
                        <div className="card border-0 rounded-0 shadow-sm" >
                            <img src={element.img} className="card-img-top" alt="" />
                                <div className="card-body py-4">
                                <div className='d-flex justify-content-between'>
                                    <h6 className="py-1 "><span className="fa fa-star text-warning pe-2"></span>{element.rating}</h6>
                                    <button className="px-0 py-0 bg-transparent  border-0 text-danger  fs-2 " onClick={() => handleRemove(element._id)}>
                                    <i className='bx bxs-bookmark'></i>
                                    </button>
                                </div>
                                <h6 className="card-title pb-4 fs-6 fw-light">{element.name}</h6>
                                <button className="btn btn-outline-danger py"><i className="fa-solid fa-play pe-2"></i>Watch Trailer</button>

                            </div>

                        </div>
                    </div>
                ))}
            </div>
        </div>

    );
};

export default Cart;