import React from 'react'
import '../App.css'
import { useDispatch,useSelector } from 'react-redux'
import { fetchMovies,fetchbollywood,fetchhollywood ,fetchByStar,fetchByGenre, searchMovies,getSearch} from '../State/moviesSlice';
import { useEffect,useState ,useMemo} from 'react';




function Filter() {

    const dispatch = useDispatch();
    // const [search ,setSearch]=useState("");
 
    // const { data: movies, status } = useSelector((state) => state.movies)
     const { paginateData: movies, search } = useSelector((state) => state.movies)
    //  console.log(search);
    // const handleSearch=(e)=>{
    //     let name=e.target.name;
    //     let value=e.target.value;
    //     console.log(name)
    //     console.log(value);
    //     setSearch({...search,[name]:value})
    // }
    //  movies = useMemo(() => movies.filter(job => job.startsWith(search),[search]))

   const handleSubmit=(event)=>{
    event.preventDefault();
    }
    const [screenSize, getDimension] = useState({
        dynamicWidth: window.innerWidth,
       
      });
      const setDimension = () => {
        getDimension({
          dynamicWidth: window.innerWidth,
        })
      }
      
      useEffect(() => {
        window.addEventListener('resize', setDimension);
        dispatch(searchMovies(search))
        return(() => {
            window.removeEventListener('resize', setDimension);
        })
        
      }, [screenSize,search])

const toggleFilter=()=>{

   let filterDisplay= document.getElementById("filter-responsives");
     if(filterDisplay.style.display==="flex"){
        console.log("click uppper")
        filterDisplay.style.display="none"
        // filterDisplay.classList.remove("filter-responsive") 
        // filterDisplay.classList.add("filter-responsive-hide") 
    }else{
        console.log("click down")
        filterDisplay.style.display="flex"
        // filterDisplay.classList.remove("filter-responsive-hide")  
        // filterDisplay.classList.add("filter-responsive")
    }


}


    return (
        <>

            <div className="filter-section me-5 shadow-sm w-25">
                <div className="filter-content">
                    <div className='d-flex justify-content-between'>
                    <h2 className="my-3 text-danger text-nowrap" >FILTER MOVIES</h2>
 <i className='bx bxs-caret-down-circle fs-1 mt-3 text-danger'id="filter-toggle" onClick={()=>toggleFilter()}></i>
                    </div>
                    <form onSubmit={handleSubmit}>
                <input type="text" className='mb-3 py-2 search text-danger px-2 w-100' value={search}  onChange={(e)=> dispatch(getSearch(e.target.value))}  placeholder='Search Movies' />
                </form>
                {console.log("sreach",search)}
                 <div className={window.innerWidth<=768 ? "filter-responsive":"d-block"}   id="filter-responsives">
                 <div className='genre'>
                    <h4 >Genre</h4>
                    <ul>
                        <li><button onClick={() => dispatch(fetchMovies())}>All</button></li>
                        <li><button onClick={() => dispatch(fetchByGenre("action"))}>Action</button></li>
                        <li><button onClick={() => dispatch(fetchByGenre("comedy"))}>Comedy</button></li>
                        <li><button onClick={() => dispatch(fetchByGenre("thriller"))}>Thriller</button></li>
                        <li><button onClick={() => dispatch(fetchByGenre("horror"))}>Horror</button></li>
                        <li><button onClick={() => dispatch(fetchByGenre("drama"))}>Drama</button></li>
                        <li><button onClick={() => dispatch(fetchByGenre("fantasy"))}>Fantasy</button></li>
                        <li><button onClick={() => dispatch(fetchByGenre("animation"))}>Animation</button></li>
                    </ul>
                    </div>
                    
                    <div className="categories me-3">
                    <h4>Category</h4>
                        <ul>
                      <li><button onClick={() => dispatch(fetchbollywood())}>bollwood</button></li>
                     <li> <button onClick={() => dispatch(fetchhollywood())}>Hollwood</button></li>
                        </ul>
                    </div>
                    <div className="ratings">
                    <h4>Rating</h4>
                    <div className="five-star rating" onClick={() => dispatch(fetchByStar(5))}>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                    </div>
                    <div className="four-star rating" onClick={() => dispatch(fetchByStar(4))}>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star "></span>
                    </div>
                    <div className="three-star rating" onClick={() => dispatch(fetchByStar(3))}>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star "></span>
                        <span className="fa fa-star"></span>
                    </div>
                    <div className="two-star rating" onClick={() => dispatch(fetchByStar(2))}>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star "></span>
                        <span className="fa fa-star"></span>
                        <span className="fa fa-star"></span>
                    </div>
                    <div className="one-star rating" onClick={() => dispatch(fetchByStar(1))}>
                        <span className="fa fa-star checked"></span>
                        <span className="fa fa-star "></span>
                        <span className="fa fa-star "></span>
                        <span className="fa fa-star"></span>
                        <span className="fa fa-star"></span>
                    </div>
                    </div>
                </div>
            </div>
  </div>
        </>
    )
}

export default Filter