import React, { useEffect } from 'react'
import { useNavigate } from 'react-router'
import { useSelector, useDispatch } from 'react-redux'
//import { fetchUsers } from '../../State/moviesSlice'
import { fetchUsers } from '../../State/UserSlice'
import { fetchMovies } from '../../State/moviesSlice'
import './style.css'
function Admin() {
    const dispatch = useDispatch();
    const navigate = useNavigate();

    const { data: users } = useSelector((state) => state.users)
    const { data: movies } = useSelector((state) => state.movies)
    useEffect(() => {
        dispatch(fetchUsers())
        dispatch(fetchMovies())
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (

        <>

            {/* <!-- Page Wrapper --> */}

            <div className="admin-dashborad container-fluid d-flex px-0">
                <div className="sidebar shadow left">
                    <h3 className="text-dark"><i className='bx bxs-dashboard'></i>DashBoard</h3>
                    <hr className="text-dark" />
                    <div className="dropend">
                        <button className="btn dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                            Users
                        </button >
                        <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                            <li><a className="dropdown-item " href="#" onClick={() => (navigate('/Users'))}>Delete</a></li>
                        </ul>
                    </div>
                    <div className="dropend">
                        <button className="btn dropdown-toggle" type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                            Movies
                        </button>
                        <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                            <li><a className="dropdown-item" href="#" onClick={() => (navigate('/Addmovie'))}>Add</a></li>
                            <li><a className="dropdown-item" href="#" onClick={() => (navigate('/Deletemovie'))}>Delete</a></li>
                        </ul>
                    </div>
                </div>


                {/* <!-- Begin Page Content --> */}
                <div className="container-fluid py-4 px-3 right">

                    {/* <!-- Page Heading --> */}
                    <div className="d-sm-flex align-items-center justify-content-between mb-4">
                        <h1 className="h3 mb-0 text-gray-800">Dashboard</h1>
                    </div>

                    {/* <!-- Content Row --> */}
                    <div className="row">

                        {/* <!-- Movies (Monthly) Card Example --> */}
                        <div className="col-xl-3 col-md-6 mb-4">
                            <div className="card border-left-primary shadow h-100 py-2" onClick={() => (navigate('/Deletemovie'))}>
                                <div className="card-body">
                                    <div className="row no-gutters align-items-center">
                                        <div className="col mr-2">
                                            <div className="text-xs font-weight-bold fs-5 text-primary text-uppercase mb-1">
                                                Total Movies</div>
                                            <div className="h5 mb-0 font-weight-bold text-gray-800">{movies.length}</div>
                                        </div>
                                        <div className="col-auto">
                                            <i className="fa-solid fa-clapperboard fa-2x text-gray-300"></i>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                        <div className="col-xl-3 col-md-6 mb-4">
                            <div className="card border-left-success shadow h-100 py-2" onClick={() => (navigate('/Users'))}>
                                <div className="card-body">
                                    <div className="row no-gutters align-items-center">
                                        <div className="col mr-2">
                                            <div className="text-xs font-weight-bold fs-5 text-success text-uppercase mb-1">
                                                Total Users  </div>
                                            <div className="h5 mb-0 font-weight-bold text-gray-800">{users.length}</div>
                                        </div>
                                        <div className="col-auto">
                                            <i className="fas fa-users fa-2x text-gray-300"></i>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>





            </div>



        </>
    )
}

export default Admin