import React from 'react'
import { useFormik } from "formik"
import { movieSchema } from '../../schemas'
import axios from "axios"
import { useAlert } from 'react-alert'
function Addmovie() {
  const alert = useAlert()
    const initialValues = {
        name:"",
        rating:"",
        genre:"",
        category:"",
        img:""
        
      }
    
      const { values, touched, errors, handleBlur,setFieldValue, handleChange, handleSubmit, isSubmitting, resetForm } = useFormik({
        initialValues: initialValues,
        validationSchema:movieSchema,
        onSubmit: async (values) => {
          const formdata = new FormData();
  // 👇 Uploading the file using the fetch API to the server
         formdata.append('testImage',values.img,values.img.name)
          formdata.append('name',values.name)
          formdata.append('rating',values.rating)
          formdata.append('genre',values.genre);
          formdata.append(' category',values.category);
          // formdata.append('img',values.img.name);
          // const response = await fetch(`http://localhost:5000/api/movies/addmovie`, {
          //   method: 'POST', // *GET, POST, PUT, DELETE, etc.
          //   headers: {
          //     "Content-Type": "multipart/form-data"   
          //   },
          //   // body: JSON.stringify(values)
          //    body: formdata
          // });

          axios.post(`/api/movies/addmovie`,formdata,{
            headers: {
                    "Content-Type": "multipart/form-data"
                    // "Content-Type": "application/json"
                     },
          }).then(res=>{
               return res;
          }).then((data)=>{console.log(data);
            alert.success("Movie added")})
            .catch((err)=> alert.error("movie not added"))
        
        
          
    
          // let data = await response.json();
          // console.log("data" , data)
          // if (data) {
          //   console.log("success")
          //   alert("movie added successfully")
    
          // } else {
          //   alert('movie is not added succesfully')
          //   console.log("fail")
          // }
          // // console.log("errors" , errors)   
          // console.log("formvalues" , values)   
          resetForm();
        }
      })
  return (
    <div className="container py-5  users-container">
    <div className="users-section shadow py-4">
       <h1 className="text-center py-3">Add Movie</h1>
      {/* Enter User id to delete: <input type="text" name="id" id="id" onMouseLeave={edValueKeyPress} /> */}
         <div className="user-content px-4">

<form onSubmit={handleSubmit}>
  <div className="mb-3">
    <label htmlFor="exampleInputEmail1" className="form-label">Movie Name</label>
    <input type="text" className="form-control" id="exampleInputEmail1" value={values.name} onChange={handleChange} onBlur={handleBlur} name='name'/>
    {console.log(values.name)}
  </div>
  <div className="mb-3">
    <label htmlFor="exampleInputEmail2" className="form-label">Movie image</label>
    <input type="file" className="form-control" name='img'onChange={(event) => {
  setFieldValue("img", event.target.files[0])}} onBlur={handleBlur} id="exampleInputEmail1" />
 
  </div>
  <div className="mb-3">
    <label htmlFor="exampleInputPassword1" className="form-label">Genre</label>
        {/* <input type="text" className="form-control" name='genre' value={values.genre} onChange={handleChange} onBlur={handleBlur} /> */}
    <select className="form-select"  name='genre' value={values.genre} onChange={handleChange} onBlur={handleBlur} >
  <option value="">Select Genre</option>
  <option value="action">Action</option>
  <option value="comedy">comedy</option>
  <option value="thriller">Thriller</option>
  <option value="horror">Horror</option>
  <option value="drama">Drama</option>
  <option value="fantasy">Fantasy</option>
  <option value="animation">Animation</option>
</select>
  </div>
  <div className="mb-3">
    <label htmlFor="exampleInputPassword4" className="form-label">Category</label>
         {/* <input type="text" className="form-control"  name='category' value={values.category} onChange={handleChange} onBlur={handleBlur} /> */}
    <select className="form-select"  name='category' value={values.category} onChange={handleChange} onBlur={handleBlur} >
  <option >Select Category</option>
  <option value="hollywood">Hollywood</option>
  <option value="bollywood">Bollywood</option>
  </select>
  </div>
  <div className="mb-3">
    <label htmlFor="exampleInputPassword5" className="form-label">Rating</label>
      {/* <input type="number" className="form-control" name='rating' value={values.rating} onChange={handleChange} onBlur={handleBlur} /> */}
    <select className="form-select" name='rating' value={values.rating} onChange={handleChange} onBlur={handleBlur} >
  <option >Select Rating</option>
  <option value="1">1</option>
  <option value="2">2</option>
  <option value="3">3</option>
  <option value="4">4</option>
  <option value="5">5</option>
</select>
  </div>

  <button type="submit" className="btn btn-danger">ADD Movie</button>
</form>
</div>
    </div>
</div>
  )
}

export default Addmovie