import React from 'react'
import { useSelector } from 'react-redux'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom'
import { LinkContainer } from 'react-router-bootstrap';
import  { useNavigate } from 'react-router'
function Header() {
  const navigate = useNavigate()
  const handleLogout=()=>{
    localStorage.removeItem('token');
    navigate('/login')
  }


  // console.warn(props.data)
  return (
    <nav className="navbar z-index-3 navbar-expand-lg navbar-dark  bg-dark" style={{ height: '80px' }}>
      <div className="container-fluid px-0">
        <Link className="navbar-brand ps-3" to="/"><span>I</span>Cenima</Link>
        <button className="navbar-toggler mx-3" type="button" data-bs-toggle="collapse" data-bs-target="#navbarScroll" aria-controls="navbarScroll" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon "></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarScroll">
          <ul className="navbar-nav me-auto px-3 my-2 my-lg-0 navbar-nav-scroll" >
            <li className="nav-item">
              <Link className="nav-link active" aria-current="page" to="/"></Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link mt-2 fs-5" to="/">Home</Link>
            </li>
            {localStorage.getItem('token') ?  <li className="nav-item">
              <Link className="nav-link mt-2 fs-5" to="/cart">Favourits</Link>
            </li>:""}
            {/* <li className="nav-item">
              <Link className="nav-link active" aria-current="page" to="/admin">Admin</Link>
            </li> */}
          </ul>
          <form className="d-flex px-3 py-3">
            {!localStorage.getItem('token') ? <div className="d-flex justify-content-center">

              <LinkContainer to="/Login">
                <Button variant="danger" className=" rounded-0 me-2 pe-3">Login</Button>
              </LinkContainer>
              <LinkContainer to="/Signup">
                <Button variant="outline-danger" className="rounded-0">Signup</Button>
              </LinkContainer>
            </div> : <Button variant="danger" onClick={handleLogout} className="rounded-0">Logout</Button>}
          </form>
        </div>
      </div>
    </nav>
  )
}

export default Header