import React, { useEffect } from 'react'
import { fetchMovies, Deletemovies, removeMovies } from '../../State/moviesSlice'
import { useSelector, useDispatch } from 'react-redux'
function Deletemovie() {
  const dispatch = useDispatch();

  const { data: movies } = useSelector((state) => state.movies)

  useEffect(() => {
    dispatch(fetchMovies());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const handleRemove = (movieId) => {
    dispatch(removeMovies(movieId))
  }
  return (
    <div className="container py-5  users-container">
      <div className="users-section shadow py-4">
        <h1 className="text-center py-3">Movies</h1>
        {/* Enter User id to delete: <input type="text" name="id" id="id" onMouseLeave={edValueKeyPress} /> */}
        <div className="user-content px-4">
          <table className="table">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Movie Id</th>
                <th scope="col">Movie Name</th>
                <th scope="col"> Rating</th>
                <th scope="col"> genre</th>
                <th scope="col">Delete Movie</th>
              </tr>
            </thead>

            <tbody>
              {movies.map((movies, id) => {
                return <tr key={movies._id}>
                  <th scope="row">{id}</th>
                  <td>{movies._id}</td>
                  <td >{movies.name}</td>
                  <td>{movies.rating}</td>
                  <td>{movies.genre}</td>
                  <td><button className="btn btn-danger" onClick={() => dispatch(Deletemovies(movies._id), handleRemove(movies._id))}>Delete</button></td>
                </tr>
              })}
            </tbody>

          </table>
        </div>
      </div>
    </div>
  )
}

export default Deletemovie