import * as Yup from "yup";



export const loginSchema=Yup.object({
       email:Yup.string().email().required("please enter your email"),
       Password:Yup.string().required('please enter your password')
       
})


export const signUpSchema=Yup.object({
    fname:Yup.string().min(3).required("fname Field is Required"),
    lname:Yup.string().min(3).required("Please enter your lname"),
    email:Yup.string().email().required("please enter your email"),
    Password:Yup.string().required('please enter your password'),
    CPassword:Yup.string().required('please enter Confirm passowrd').oneOf(
          [Yup.ref("Password")],
          "Both password need to be the same"
        )
})


export const movieSchema=Yup.object({
       name:Yup.string().min(5).required("Field is Required"),
       category:Yup.string().required("Category field is Required"),
       rating:Yup.number().required("rating field is Required"),
       img:Yup.string().required('img field is Required'),
       genre:Yup.string().required('genre field is Required'),
       
   })